<template>
  <div v-if="topic" class="topic">
    <base-panel>
      <template slot="panel-content">
        <div class="topic-header">
          <div class="topic-header-top">
            <el-tag
              class="tag"
              size="mini"
              effect="dark"
              type="success"
              v-if="topic.top || topic.good"
              >{{ topic.top ? "置顶" : "精华" }}</el-tag
            >
            <h3>{{ topic.title }}</h3>
            <el-button @click="collect" type="success" v-if="accesstoken">
              {{ topic.is_collect ? "取消收藏" : "收藏" }}
            </el-button>
          </div>
          <p>
            <span class="icon-dian iconfont disc"></span>
            <span class="spanText">发布于{{ topic.create_at | fromNow }}</span>
            <span class="icon-dian iconfont disc"></span>
            <span class="spanText">作者{{ topic.author.loginname }}</span>
            <span class="icon-dian iconfont disc"></span>
            <span class="spanText">{{ topic.visit_count }}次浏览</span>
            <span class="icon-dian iconfont disc"></span>
            <span class="spanText">来自{{ topic.tab | tabTextTransform }}</span>
          </p>
        </div>
        <div class="topic-content" v-html="topic.content"></div>
      </template>
    </base-panel>
    <base-panel class="comment-box" v-if="topic.reply_count > 0">
      <template slot="panel-head">
        <div class="comment-head">
          <span>{{ topic.reply_count }}回复</span>
        </div>
      </template>
      <template slot="panel-content">
        <div class="comment-list">
          <div
            :class="[
              'comment-item',
              { active: commentLikeMost.includes(commet.id) },
            ]"
            v-for="(commet, index) in topic.replies"
            :key="commet.id"
          >
            <router-link :to="`/user/${commet.author.loginname}`">
              <img :src="commet.author.avatar_url" alt="" width="40px" />
            </router-link>
            <div class="comment-content">
              <span class="info">
                <span>
                  {{ commet.author.loginname }}
                  &nbsp;&nbsp;{{ index + 1 }}楼
                  {{ commet.create_at | fromNow }}
                  <span class="icon-dian iconfont disc"></span>
                  &nbsp;&nbsp;
                </span>
                <span v-if="accesstoken || commet.ups.length"
                  >{{ commet.ups.length }}
                  <span
                    @click="like(commet)"
                    class="icon-dianzan iconfont"
                  ></span>
                </span>
              </span>
              <span class="text" v-html="commet.content"></span>
            </div>
          </div>
        </div>
      </template>
    </base-panel>
    <base-panel v-if="accesstoken">
      <template slot="panel-head"> 添加回复 </template>
      <template slot="panel-content">
        <div>
          <textarea class="qinlunhufu" v-model="commentText"> </textarea>
          <el-button @click="reply" type="primary">主要按钮</el-button>
        </div>
      </template>
    </base-panel>
  </div>
</template>

<script>
import moment from  "moment"
import basePanel from "../components/layout/basePanel.vue";
import {
  getTopicId,
  collected,
  de_collected,
  zan,
  reply,
} from "../plugins/api";
export default {
  components: { basePanel },
  props: ["id"],
  data() {
    return {
      topic: null,
      accesstoken: "",
      commentText: "",
    };
  },
  async created() {
    const accesstoken = sessionStorage.getItem("accesstoken");
    if (accesstoken) {
      this.accesstoken = accesstoken;
    }
    const res = await getTopicId(this.id, accesstoken || null);
    this.topic = res.data;
    // console.log(res.data);
  },
  computed: {
    commentLikeMost() {
      if (this.topic.replies.length === 0) {
        return [];
      }
      const arr = this.topic.replies
        .slice(0)
        .sort((a, b) => b.ups.length - a.ups.length);

      // 存id的
      const commentIds = [];
      const maxOne = arr[0].ups.length;
      if (maxOne > 2) {
        const oneArr = arr.filter((ele) => ele.ups.length === maxOne);
        oneArr.forEach((element) => {
          commentIds.push(element.id);
        });

        const maxTwo = arr[oneArr.length].ups.length;
        if (maxTwo > 2) {
          const towArr = arr.filter((ele) => ele.ups.length === maxTwo);
          towArr.forEach((element) => {
            commentIds.push(element.id);
          });
        }
        return commentIds;
      }
      return [];
    },
  },
  methods: {
    async collect() {
      if (this.topic.is_collect) {
        // 取消
        await de_collected(this.accesstoken, this.topic.id);
        this.topic.is_collect = false;
      } else {
        // 收藏
        await collected(this.accesstoken, this.topic.id);
        this.topic.is_collect = true;
      }
    },
    async like(comment) {
      const accesstoken = this.accesstoken;
      if (accesstoken) {
        var res = await zan(comment.id, accesstoken);
        console.log(res);
        const id = JSON.parse(sessionStorage.getItem("userInfo")).id;
        if (res.action === "up") {
          comment.is_uped = true;
          comment.ups.push(id);
        } else {
          comment.is_uped = false;
          comment.ups = comment.ups.filter((ele) => ele !== id);
        }
      }
    },
    async reply() {
      const { topic, commentText, accesstoken } = this;
      await reply(topic.id, accesstoken, commentText);
      const res = await getTopicId(this.id, accesstoken );
      this.topic = res.data;
      this.commentText = ''
      
      const a = this.topic.replies[this.topic.replies.length-1].create_at
      const b = moment(a).format('MMMM Do YYYY, h:mm:ss a');
      console.log(b);
      console.log(a);
    },
  },
};
</script>

<style lang="less" scoped>
.topic {
  .topic-header {
    background-color: #fff;
    padding: 20px;
    border-bottom: 1px solid #ccc;
    p {
      display: flex;
      span {
        color: #d5bebe;
      }
      .disc {
        margin-top: -2px;
        font-size: 20px;
        margin-left: 5px;
      }
      .spanText {
        font-size: 12px;
        color: #ccc;
      }
    }

    .topic-header-top {
      margin-bottom: 10px;
      display: flex;
      align-items: center;
      .tag {
        margin-right: 23px;
      }
      h3 {
        font-size: 30px;
      }
    }
  }
  .topic-content {
    line-height: 1.5;
    background-color: #fff;
    img {
      width: 100%;
    }
  }
  .comment-box {
    .comment-head {
      // background-color: #ccc;
      line-height: 40px;
      padding-left: 10px;
    }
    .comment-list {
      .active {
        background-color: #b9b2b2;
      }
      .comment-item {
        display: flex;
        padding: 10px 10px 20px;
        align-items: flex-start;

        border-bottom: 1px solid #000;
        img {
          width: 40px;
          border-radius: 5px;
          margin-right: 15px;
        }
      }
      .comment-content {
        flex-grow: 1;
        .info {
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }
  .qinlunhufu {
    width: 1400px;
    background-color: #e8c3c3;
    font-size: 20px;
    padding: 10px 10px;
  }
  .qinlunhufuBtn {
    width: 100px;
    height: 50px;
  }
}
</style>
